.slides_item {
    height: 300px;
    position: relative;
    overflow: hidden; }
@media (min-width: 768px) {
    .slides_item {
        height: auto;
        max-height: calc(100vh - 80px); } }

.slides_item-img {
    display: block;
    max-width: 100%;
    width: 100%;
    height: 100%; }

.slides_item-caption {
    position: absolute;
    width: 100%;
    padding: 16px; }
@media (max-width: 767px) {
    .slides_item-caption {
        transform: translateY(-50%) translateX(-50%);
        top: 50%;
        left: 50%; } }

@media (min-width: 768px) {
    .slides_item-caption {
        max-width: 60%;
        width: auto;
    }

    .slides_item-caption--top-left {
        top: 16px;
        left: 16px;
    }

    .slides_item-caption--top-middle {
        top: 16px;
        transform: translateX(-50%);
        left: 50%;
    }

    .slides_item-caption--top-right {
        top: 16px;
        right: 16px;
    }

    .slides_item-caption--middle-left {
        top: 50%;
        left: 16px;
        transform: translateY(-50%);
    }

    .slides_item-caption--middle-middle {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .slides_item-caption--middle-right {
        top: 50%;
        transform: translateY(-50%);
        right: 16px;
    }

    .slides_item-caption--bottom-left {
        bottom: 16px;
        left: 16px;
    }

    .slides_item-caption--bottom-middle {
        bottom: 16px;
        transform: translateX(-50%);
        left: 50%;
    }

    .slides_item-caption--bottom-right {
        bottom: 16px;
        right: 16px;
    }
}

.slides_item-caption--back-drop {
    background-color: rgba(255, 255, 255, 0.8); }

.slides_item-caption-content {
    text-align: inherit;
    margin-bottom: 16px; }

.button-base, .item-more {
    line-height: 43px;
    text-transform: uppercase;
    font-size: 18px;
    color: #fff;
    border: none;
    background-color: transparent;
    vertical-align: middle;
    display: inline-block;
    padding: 0 18px;
    position: relative;
    left: 5px;
    z-index: 1;
    transition: all .2s; }
.button-base:after, .item-more:after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: #73af19;
    transform: skew(-15deg);
    z-index: -1; }
.button-base:hover, .item-more:hover {
    color: #fff !important; }
.button-base:hover:after, .item-more:hover:after {
    background-color: #8fbf47; }
.button-base:active, .item-more:active {
    top: 3px; }


.ckeditor-image .cke_toolbar_last,
.ckeditor-image .cke_toolgroup,
.ckeditor-image a.cke_button
{
    width: calc(100% - 7px);
}
.ckeditor-image .cke_toolgroup{
    margin: 4px;
}
.ckeditor-image .cke_top {
     padding: 0;
}
.ckeditor-image .cke_bottom.cke_reset_all {
    display: none;
}
